<template>
  <div
    v-if="RouteTerkini.name != 'login' && RouteTerkini.name != 'CusDaftarView'"
  >
    <Header />
  </div>
  <div v-show="loader" id="preloader"></div>
  <div class="pyro">
    <div class="before"></div>
    <div class="after"></div>
  </div>
  <router-view
    :ipAdress="ipAddress"
    :transaksi="transaksi"
    :dataHutang="dataHutang"
  ></router-view>
  <div
    v-if="RouteTerkini.name != 'login' && RouteTerkini.name != 'CusDaftarView'"
  >
    <BottomNavbar2 />
  </div>
</template>

<script>
// @ is an alias to /src
import BottomNavbar from "@/components/BottomNavbar.vue";
import BottomNavbar2 from "@/components/BottomNavbar2.vue";
import Header from "@/components/Header.vue";

import Swal from "sweetalert2";
import axios from "axios";

export default {
  components: {
    Header,
    BottomNavbar,
    BottomNavbar2,
  },
  data() {
    return {
      RouteTerkini: this.$router.currentRoute,
      user: JSON.parse(localStorage.getItem("SessionUser")),
      ipAddress: "https://adxpaybackend.arimbawadx.my.id/",
      // ipAddress: "http://127.0.0.1:8000/",
      transaksi: { mutasi_hari_ini: null, semua_mutasi_sebelumnya: null },
      dataHutang: { data_hutang: null, total_hutang: null },
    };
  },
  methods: {
    reloadSessionCus() {
      axios({
        method: "get",
        url:
          this.ipAddress +
          "api/get-data-cus/" +
          this.user.id +
          "?username=" +
          this.user.username,
      })
        .then(function (response) {
          if (response.data.message == "Tidak ada akses!") {
            localStorage.removeItem("SessionUser");
            window.location.replace("/login");
            Swal.fire({
              icon: "success",
              title: "Akses ditolak!",
              text: "Goodbye",
              showConfirmButton: false,
              timer: 1500,
            });
          } else {
            // handle success
            localStorage.setItem(
              "SessionUser",
              JSON.stringify(response.data.data)
            );
          }
        })
        .catch(function (response) {
          //handle error
          console.log(response);
        });
    },
    reloadTransaksi() {
      axios({
        method: "get",
        url:
          this.ipAddress +
          "api/get-data-trx-cus?username=" +
          this.user.username,
      })
        .then((response) => {
          //handle success
          this.loader = true;
          this.transaksi.mutasi_hari_ini = response.data.mutasi_hari_ini;
          this.transaksi.semua_mutasi_sebelumnya =
            response.data.semua_mutasi_sebelumnya;
          // console.log(this.transaksi.semua_mutasi_sebelumnya);
        })
        .catch((response) => {
          //handle error
          console.log(response);
        })
        .finally(() => (this.loader = false));
    },
    reloadHutang() {
      axios({
        method: "get",
        url:
          this.ipAddress + "api/get-data-hutang?username=" + this.user.username,
      })
        .then((response) => {
          //handle success
          this.dataHutang = response.data;
          // console.log(this.dataHutang);
        })
        .catch((response) => {
          //handle error
          console.log(response);
        });
    },
  },
  beforeMount() {
    this.loader = true;
  },
  mounted() {
    this.loader = false;
    if (localStorage.getItem("SessionUser") == null) {
      this.$router.replace("/login");
    } else {
      this.reloadTransaksi();
      this.reloadHutang();
      this.reloadSessionCus();
    }
  },
  updated() {
    // this.reloadTransaksi();
  },
};
</script>

