import { createRouter, createWebHistory } from "vue-router";
import HomeView from "../views/HomeView.vue";

const routes = [
  {
    path: "/",
    redirect: "/customer/dashboard",
  },
  {
    path: "/about",
    name: "about",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/AboutView.vue"),
  },
  {
    path: "/login",
    name: "login",
    component: () => import("../views/LoginView.vue"),
  },
  {
    path: "/customer/dashboard",
    name: "CusDashboardView",
    component: () => import("../views/Customers/DashboardView.vue"),
  },
  {
    path: "/customer/data-transaksi",
    name: "CusDataTransaksiView",
    component: () => import("../views/Customers/DataTransaksiView.vue"),
  },
  {
    path: "/customer/profil",
    name: "CusProfileView",
    component: () => import("../views/Customers/ProfileView.vue"),
  },
  {
    path: "/customer/count",
    name: "CusCountView",
    component: () => import("../views/Customers/CountView.vue"),
  },
  {
    path: "/customer/transaksi/piu1",
    name: "CusTransaksiPIU1View",
    component: () => import("../views/Customers/TransaksiPIU1View.vue"),
  },
  {
    path: "/customer/transaksi/pd",
    name: "CusTransaksiPDView",
    component: () => import("../views/Customers/TransaksiPDView.vue"),
  },
  {
    path: "/customer/transaksi/tl",
    name: "CusTransaksiTLView",
    component: () => import("../views/Customers/TransaksiTLView.vue"),
  },
  {
    path: "/customer/transaksi/tb",
    name: "CusTransaksiTBView",
    component: () => import("../views/Customers/TransaksiTBView.vue"),
  },
  {
    path: "/customer/transaksi/dana",
    name: "CusTransaksiDANAView",
    component: () => import("../views/Customers/TransaksiDANAView.vue"),
  },
  {
    path: "/customer/transaksi/ovo",
    name: "CusTransaksiOVOView",
    component: () => import("../views/Customers/TransaksiOVOView.vue"),
  },
  {
    path: "/customer/transaksi/sp",
    name: "CusTransaksiSPView",
    component: () => import("../views/Customers/TransaksiSPView.vue"),
  },
  {
    path: "/customer/transaksi/gp",
    name: "CusTransaksiGPView",
    component: () => import("../views/Customers/TransaksiGPView.vue"),
  },
  {
    path: "/customer/transaksi/lj",
    name: "CusTransaksiLAView",
    component: () => import("../views/Customers/TransaksiLAView.vue"),
  },
  {
    path: "/customer/transaksi/wi",
    name: "CusTransaksiWIView",
    component: () => import("../views/Customers/TransaksiWIView.vue"),
  },
  {
    path: "/customer/transaksi/vgml",
    name: "CusTransaksiVGMLView",
    component: () => import("../views/Customers/TransaksiVGMLView.vue"),
  },
  {
    path: "/customer/transaksi/vgff",
    name: "CusTransaksiVGFFView",
    component: () => import("../views/Customers/TransaksiVGFFView.vue"),
  },
  {
    path: "/customer/bukti-transaksi/:id",
    name: "CusBuktiTransaksiView",
    component: () => import("../views/Customers/BuktiTransaksiView.vue"),
  },
  {
    path: "/customer/daftar",
    name: "CusDaftarView",
    component: () => import("../views/Customers/DaftarView.vue"),
  },
  {
    path: "/customer/transaksi-pascabayar/tl",
    name: "CusTransaksiTLPascaView",
    component: () => import("../views/Customers/TransaksiTLPascaView.vue"),
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
